.wap-gallery-2b {
    
    .wap-gallery-img-container {
      display: flex;
      flex-wrap: wrap;
      height: 100%;
      position: relative;
  
      .photo-container {
        flex-basis: 33.33%;
  
        img {
          width: 100%;
          position: relative;
          aspect-ratio: 3/2;
          height: 100%;
          background-position: center;
          object-fit: cover;
          transition: filter 0.1s;
  
          &:hover {
            filter: brightness(70%);
          }
  
        }
  
        &:first-child {
          flex-basis: 100%;
  
          img {
            width: 100%;
            aspect-ratio: 3/1;
          }
        }
      }
    }
  }
  
  
  
  
  
  
  
