.home-header {
    position: fixed;
    width: 100%;
    color: white;
    background-color: rgba(255, 255, 255, 0);
    transition: 0.45s all ease;
    z-index: 5;

    // &.header-down {
    //     color: $almost-black;
    //     background-color: rgb(255, 255, 255);
    //     box-shadow: rgba(0, 0, 0, 0.12) 0px 2px 8px 1px;
    // }



    .header-txt-container {
        display: flex;
        align-items: bottom;
        font-family: Arial, Helvetica, sans-serif;
        padding: 5px $mobile-padding;

        @media (min-width: $mobile-breakpoint) {
            padding: 5px $narrow-padding;
        }

        @media (min-width: $narrow-breakpoint) {
            padding: 5px $normal-padding;
        }

        // margin-inline: 40px;
        // max-width: 1500px;
    }


    .pages-container>* {
        text-decoration: none;

    }

    .pages-container {
        display: flex;
        align-items: baseline;
    }

    // &:not(:first-child) {
    //     margin-inline-start: 20px;
    // }



    .user-info {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .user-name {
        text-transform: capitalize;
    }

    .profile-img {
        font-size: 1.5rem;
    }

    .logo {
        text-transform: capitalize;
        font-family: caviar-dreams-Bold;
        margin: 0;
        font-size: 1.5rem;
        padding: 5px 0;
        text-decoration: none;
    }

    nav {
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        justify-content: space-between;
        flex-basis: 100%;

        .header-profile-container {
            font-size: 0.8rem;
            margin: 0;
            display: flex;
            align-items: center;
            gap: 3px;
            justify-content: center;
            border: 1px solid rgba(255, 255, 255, 0.648);
            // background-color: rgba(255, 255, 255, 0.699);
            // padding: 3px 3px 3px 0;
            border-radius: 2rem;

            >* {
                text-decoration: none;
            }
        }

        .login-container {
            display: flex;
            gap: 20px;
        }


        .login-btn {
            font-size: 1rem;
            margin: 0;
            display: flex;
            align-items: center;
            justify-content: center;

            >* {
                text-decoration: none;
            }
        }

        .signup-btn {
            @include btn($bg: inherit, $clr: $almost-white, $padding: em(4px) em(8px), $hover: rgba(255, 255, 255, 0.134));
            border: 0.5px solid rgba(255, 255, 255, 0.795);
            font-size: 1rem;
            margin: 0;
            display: flex;
            align-items: center;
            justify-content: center;

            >* {
                text-decoration: none;
            }
        }





        .pages-container {
            display: flex;
            gap: 20px;
        }

        >* {
            font-size: 0.8rem;
            text-decoration: none;
        }

    }

    nav .active {
        color: $clr1;
    }

    &.header-down {
        color: $almost-black;
        background-color: rgb(255, 255, 255);
        box-shadow: rgba(0, 0, 0, 0.12) 0px 2px 8px 1px;

        .signup-btn {
            // @include btn($bg: inherit, $clr: $almost-black, $padding: em(4px) em(8px), $hover: rgba(255, 255, 255, 0.134));
            color: $almost-black;
            border: 0.5px solid rgba(27, 27, 27, 0.795);

            &:hover {
                background-color: rgba(27, 27, 27, 0.089);
            }

        }
    }

    &.header-down{
        color: $almost-black;
        background-color: rgb(255, 255, 255);
        box-shadow: rgba(0, 0, 0, 0.12) 0px 2px 8px 1px;

        .header-profile-container {
            border: 1px solid rgb(97, 97, 97);
        }
    }
}

@media (min-width: 1500px) {
    .app-header .header-txt-container {
        margin: auto;
    }
}